<script>
import axios from "axios";
export default {
  data() {
    return {
      info: null,
      img_Url: [],
      section_left:{
        text:[" SẢN PHẨM BẢO HIỂM"]
      },
    }
  },
   mounted () {
    axios
      .post('https://api.miinasian.vn/public/banners')
      .then(response => (this.info = response))
  },
  watch:{
    info(value){
      let _ = this;
      value.data.d.forEach(function(item){
        _.img_Url.push(item.Url);
      });
    }

  }

  
};
</script>

