import Vue from "vue";
import VueRouter from "vue-router";
import Sanpham from "../views/Sanpham.vue";
import Muachung from "../views/Muachung.vue";
import Boithuong from "../views/Boithuong.vue";
import Huongdanvatuvan from "../views/Huongdanvatuvan.vue";
import Vechungtoi from "../views/Vechungtoi.vue";
import Macdinh from "../views/chidlrent_of_sanpham/default.vue";
import Hiemngheo from "../views/chidlrent_of_sanpham/hiemngheo.vue";
import Hocsinh from "../views/chidlrent_of_sanpham/hocsinh.vue";
import Huutri from "../views/chidlrent_of_sanpham/huutri.vue";
import Suckhoe from "../views/chidlrent_of_sanpham/suckhoe.vue";
import Tainan from "../views/chidlrent_of_sanpham/tainan.vue";
import Ungthu from "../views/chidlrent_of_sanpham/ungthu.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "sanpham",
    component: Sanpham,
    children: [
      {
        path: '',
        component: Macdinh
      },
      {
        path:'hiemngheo',
        component: Hiemngheo
      },
      {
        path:'hocsinh',
        component: Hocsinh
      },
      {
        path:'huutri',
        component: Huutri
      },
      {
        path:'suckhoe',
        component: Suckhoe
      },
      {
        path:'tainan',
        component: Tainan
      },
      {
        path:'ungthu',
        component: Ungthu
      },
      
    ]
  },
  {
    path: "/muachung",
    name: "muachung",
    component: Muachung
  },
  {
    path: "/boithuong",
    name: "boithuong",
    component: Boithuong
  },
  {
    path: "/huongdanvatuvan",
    name: "huongdanvatuvan",
    component: Huongdanvatuvan
  },
  {
    path: "/vechungtoi",
    name: "vechungtoi",
    component: Vechungtoi
  }
];

const router = new VueRouter({
  routes
});

export default router;
