<template>
    <b-carousel
          id="carousel-1"
          :interval="4000"
          controls
          indicators
          class="h-100"
        >
          <!-- Text slides with image -->
          <b-carousel-slide 
            id="img-height"
            v-for="(item, index) in img_link_sectionright"
            v-bind:key="index"
          >
          <template v-slot:img>
            <img
              class="d-block img-fluid w-100 h-100 round"
              :src="`https://api.miinasian.vn/res/${prop_img_url[index]}`"
              alt="image slot"
            >
          </template>
        </b-carousel-slide>
    </b-carousel>
</template>
<script>

export default {
  props:{
    prop_img_url:{
      type: Array,
    }
  },
  data() {
    return {
      img_link_sectionright: ["https://picsum.photos/1024/480/?image=55", "https://picsum.photos/1024/480/?image=52", "https://picsum.photos/1024/480/?image=54", "https://picsum.photos/1024/480/?image=58", "https://picsum.photos/1024/480/?image=55"]
    }
  },
  
};
</script>
<style scoped>
@media only screen and (max-width: 575px) {
  #img-height{
    height:250px;
  }
}
@media only screen and (min-width: 576px) {
  #img-height{
    height:350px;
  }
}

@media only screen and (min-width: 992px) {
  #img-height{
    height:500px;
  } 
}
</style>