<template>
  <header>
    <div>
      <b-navbar class="h-100" toggleable="xl" type="light" variant="success">
        <!-- logo OK -->
        <b-navbar-brand class="ml-5">
          <img :src="header.logo_header" alt="logo_minn" />
        </b-navbar-brand>

        <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>
        <!-- menu collaspe -->
        <b-collapse id="nav-collapse" is-nav>
          <b-navbar-nav class="d-xl-none text-center horizontal-nav-border">
            <b-nav-item
              v-bind:key="index"
              v-for="(item, index) in menu_header.contents"
              :to="menu_header.links_menu[index]"
              @click="clickHorizontalNav(index)"
            >
              {{ item }}
            </b-nav-item>
          </b-navbar-nav>

          <!-- Right aligned nav items -->

          <b-dropdown
            right
            id="language-icon"
            class="ml-auto"
            variant="success"
          >
            <template v-slot:button-content>
              <p
                class="d-inline pr-1"
                style="font-family: 'Merriweather', serif; color: white"
              >
                Ngôn Ngữ:
              </p>
              <img
                class="img-fluid d-inline"
                :src="`http://miin.asia/asset/image/${currentLang}-flag.png`"
                width="30"
              />
            </template>
            <b-dropdown-item @click.prevent="currentLang = 'viet'">
              <img class="img-fluid p-2" :src="header.img_dropdown_vi" />
              <p class="text-center">Việt Nam</p>
            </b-dropdown-item>
            <b-dropdown-item @click.prevent="currentLang = 'eng'">
              <img class="img-fluid p-2" :src="header.img_dropdown_en" />
              <p class="text-center">English</p>
            </b-dropdown-item>
          </b-dropdown>

          <!-- Icons socical -->
          <b-navbar-nav
            class="mr-lg-5 mt-2 justify-content-md-center justify-content-sm-center justify-content-center"
            id="icon-social-parent"
          >
            <b-nav-item
              v-for="(item, index) in headerIcon.nameIcon"
              :key="index"
              id="icon-social"
              :href="headerIcon.linkIcon[index]"
            >
              <font-awesome-icon
                :icon="['fab', item]"
                class="css-icon-social"
              />
            </b-nav-item>
          </b-navbar-nav>
        </b-collapse>
      </b-navbar>
    </div>
    <!--Horizontal menu-->
    <div class="container-fluid m-0 p-0">
      <div class="row">
        <b-nav
          id="horizontal-nav1"
          class=" col-6 ml-5 mt-4 border rounded justify-content-center align-items-center"
          style="height:50px"
        >
          <b-nav-item
            class="h-100"
            style="color:black"
            v-for="(item, index) in menu_header.contents.slice(0, 5)"
            :key="index"
            :to="menu_header.links_menu[index]"
            @click="clickHorizontalNav(index)"
            v-bind:class="[checkClickNav(index) ? 'clicked' : '']"
          >
            <span class="text-dark">{{ item }}</span>
          </b-nav-item>
        </b-nav>
        <div class="col-4 mt-3" id="horizontal-nav2">
          <b-list-group
            style="height:60px"
            horizontal
            class="d-flex flex-row justify-content-between"
          >
            <b-list-group-item
              class="d-flex align-items-center border-0"
              v-for="(item, index) in headerIcon.horizontalIcon"
              :key="index"
              style="cursor:pointer;"
            >
              <b-avatar class="bg-light">
                <font-awesome-icon
                  :icon="['fas', item]"
                  style="color:green; font-size:40px;"
                >
                </font-awesome-icon>
              </b-avatar>
              <div class="mr-auto d-flex flex-column pl-2">
                <span class=" text-dark">{{
                  index == 0 ? menu_header.contents[5] : "Hotline"
                }}</span>
                <span class=" text-dark">{{
                  index == 0 ? menu_header.contents[6] : header.hotLine
                }}</span>
              </div>
            </b-list-group-item>
          </b-list-group>
        </div>
      </div>
    </div>
  </header>
</template>

<script>
export default {
  data() {
    return {
      index_nav: 0,
      header: {
        logo_header: "http://miin.asia/asset/image/logo.png",
        img_dropdown_vi: "http://miin.asia/asset/image/viet-flag.png",
        img_dropdown_en: "http://miin.asia/asset/image/eng-flag.png",
        hotLine: "0974745553"
      },
      menu_header: {
        contents: [
          "Sản Phẩm",
          "Mua Chung",
          "Bồi Thường",
          "Hướng Dẫn và Tư Vấn",
          "Về Chúng Tôi",
          "Đăng Nhập",
          "Đăng Ký"
        ],
        links_menu: [
          "/",
          "/muachung",
          "/boithuong",
          "/huongdanvatuvan",
          "/vechungtoi"
        ]
      },
      currentLang: "viet",
      headerIcon: {
        linkIcon: ["#", "#", "#", "#", "#"],
        nameIcon: [
          "facebook-square",
          "twitter-square",
          "google-plus-square",
          "instagram",
          "linkedin"
        ],
        horizontalIcon: ["user-circle", "phone-square"]
      }
    };
  },
  methods: {
    clickHorizontalNav: function(index) {
      this.index_nav = index;
    },
    checkClickNav: function(index) {
      if (this.index_nav == index) {
        return true;
      } else {
        false;
      }
    }
  }
};
</script>

<style scoped>
@import url("https://fonts.googleapis.com/css2?family=Merriweather&display=swap");

@media only screen and (max-width: 1100px) {
  #language-icon,
  #horizontal-nav1,
  #horizontal-nav2 {
    display: none;
  }
  #icon-social-parent {
    flex-direction: row;
  }
  #icon-social {
    padding-right: 1rem;
  }
  .css-icon-social {
    color: white;
    font-size: 40px;
  }
  .horizontal-nav-border {
    border-bottom: solid 1px #c5c5c5;
  }
  .navbar-light .navbar-nav .nav-link {
    color: white;
  }
}

@media only screen and (min-width: 1100px) {
  .css-icon-social {
    color: white;
    font-size: 20px;
  }
}
#collaspe-nav::after {
  display: block;
  clear: both;
  content: "";
}
#horizontal-nav2 {
  position: relative;
  right: -10%;
}
#language-icon {
  margin-top: 8px;
}

.menu-main {
  border: solid 1px #c5c5c5;
  border-radius: 5px;
  margin-top: 20px;
}
.list-unstyled {
  padding-left: 0;
  list-style: none;
}
.clicked {
  background-color: #2b9348;
}
#horizontal-nav1 .clicked::after {
  display: inline-block;
  position: relative;
  content: "";
  border-top: 10px solid;
  border-right: 10px solid transparent;
  border-bottom: 0;
  border-left: 10px solid transparent;
  top: 0px;
  left: 45%;
  color: #2b9348;
}
span.text-dark:hover{
  color: blue !important;
}
</style>
