<template>
    <div class="w-100 d-flex flex-row justify-content-center align-items-center">
      <div   class="d-flex flex-row justify-content-center align-items-center" >
        <div class="mr-2 d-flex flex-column justify-content-center align-items-center">
          <div class="number-size">{{ days |twoDigits }}:</div>
          <div class="text-size">{{ wordString.day }}</div>
        </div>
        <div class=" mr-2 d-flex flex-column justify-content-center align-items-center">
          <div class="number-size">{{ hours |twoDigits}}:</div>
          <div class="text-size">{{ wordString.hours|twoDigits }}</div>
        </div>
        <div class=" mr-2 d-flex flex-column justify-content-center align-items-center">
          <div class="number-size">{{ minutes|twoDigits }}:</div>
          <div class="text-size">{{ wordString.minutes }}</div>
        </div>
        <div class="d-flex flex-column justify-content-center align-items-center">
          <div class="number-size">{{ seconds |twoDigits }}</div>
          <div class="text-size">{{ wordString.seconds |twoDigits}}</div>
        </div>
      </div>
      <div class="ml-3">
        <p style="font-size:13px">{{message}}</p>
      </div>
    </div>
</template>

<script>
export default {
    props: ['starttime','endtime','trans'] ,
    data: function(){
        return{
        timer:"",
        wordString: {},
        start: "",
        end: "",
        interval: "",
        days:"",
        minutes:"",
        hours:"",
        seconds:"",
        message:"",
        statusType:"",
        statusText: "",
        
        };
    },
  created: function () {
        this.wordString = JSON.parse(this.trans);
    },
  mounted(){
    this.start = new Date(this.starttime).getTime();
    this.end = new Date(this.endtime).getTime();
    // Update the count down every 1 second
    this.timerCount(this.start,this.end);
    this.interval = setInterval(() => {
        this.timerCount(this.start,this.end);
    }, 1000);
  },
  filters: {
    twoDigits(value) {
      if (value.toString().length <= 1) {
        return "0" + value.toString()
      }
      return value.toString()
    }
  },
  methods: {
    check(){
      if(this.statusType == 'expired'){
        return true
      }
      else{
        return false
      }
    },
    timerCount: function(start,end){
        // Get todays date and time
        var now = new Date().getTime();

        // Find the distance between now an the count down date
        var distance = start - now;
        var passTime =  end - now;

        if( passTime < 0){
            this.message = this.wordString.expired;
            this.statusType = "expired";
            this.statusText = this.wordString.status.expired;
            clearInterval(this.interval);
            return;

        }else if(distance < 0 && passTime > 0){
            this.calcTime(passTime);
            this.message = this.wordString.running;
            this.statusType = "running";
            this.statusText = this.wordString.status.running;

        } else if( distance > 0 && passTime > 0 ){
            this.calcTime(distance+passTime); 
            this.message = this.wordString.upcoming;
            this.statusType = "upcoming";
            this.statusText = this.wordString.status.upcoming;
        }
    },
    calcTime: function(dist){
      // Time calculations for days, hours, minutes and seconds
        this.days = Math.floor(dist / (1000 * 60 * 60 * 24));
        this.hours = Math.floor((dist % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
        this.minutes = Math.floor((dist % (1000 * 60 * 60)) / (1000 * 60));
        this.seconds = Math.floor((dist % (1000 * 60)) / 1000);
    }
    
  }


}
</script>

<style scoped>
.display{
  display:none;
}
.text-size{
  font-size: 10px;
}
.number-size{
  font-size: 12px;
  font-weight: bold;
}


</style>